import React, { useState } from 'react'
import improvementPrompts from '../ImprovementPrompts'

const ImprovementCard = () => {
    const [prompt, setPrompt] = useState(improvementPrompts[0])
    const getRandomPrompt = () => {
        let num = Math.floor(Math.random() * improvementPrompts.length)
        let newPrompt = improvementPrompts[num]

        return newPrompt
    }

    return (
        <div
            className="card-container improvement"
            onClick={() => setPrompt(getRandomPrompt)}
        >
            <h6>{prompt.prompt}</h6>
            <footer className="card-footer">
                Retros Against Humanity | Improvement Cards
           </footer>
        </div>
    )
}

export default ImprovementCard