import React, { useState } from 'react'
import goodPrompts from '../GoodPrompts'

const GoodCard = () => {
    const [prompt, setPrompt] = useState(goodPrompts[0])
    const getRandomPrompt = () => {
        let num = Math.floor(Math.random() * goodPrompts.length)
        let newPrompt = goodPrompts[num]

        return newPrompt
    }

    return (
        <div
            className="card-container good"
            onClick={() => setPrompt(getRandomPrompt)}
        >
            <h6>{prompt.prompt}</h6>
            <footer className="card-footer">
                Retros Against Humanity | Good Cards
           </footer>
        </div>
    )
}

export default GoodCard