import React, { useState } from 'react'
import badPrompts from '../BadPrompts'

const BadCard = () => {
    const [prompt, setPrompt] = useState(badPrompts[0])
    const getRandomPrompt = () => {
        let num = Math.floor(Math.random() * badPrompts.length)
        let newPrompt = badPrompts[num]

        return newPrompt
    }

    return (
        <div
            className="card-container bad"
            onClick={() => setPrompt(getRandomPrompt)}
        >
            <h6>{prompt.prompt}</h6>
            <footer className="card-footer">
                Retros Against Humanity | Bad Cards
           </footer>
        </div>
    )
}

export default BadCard