import React, { useState } from 'react'
import sprintStarPrompts from '../SprintStarPrompts'

const ImprovementCard = () => {
    const [prompt, setPrompt] = useState(sprintStarPrompts[0])
    const getRandomPrompt = () => {
        let num = Math.floor(Math.random() * sprintStarPrompts.length)
        let newPrompt = sprintStarPrompts[num]

        return newPrompt
    }

    return (
        <div
            className="card-container sprint-star"
            onClick={() => setPrompt(getRandomPrompt)}
        >
            <h6>{prompt.prompt}</h6>
            <footer className="card-footer">
                Retros Against Humanity | Sprint Star Cards
           </footer>
        </div>
    )
}

export default ImprovementCard